.typingAnimation {
  font-size: 12px;
  font-weight: bold;
  font-family: Arial, sans-serif;
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: 0.15em;
  margin: 0 auto;

  color: white;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

.typing-animation {
  border-right: 0.15em solid orange;
  animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}
@media screen and (min-width: 769px) {
}

@media screen and (min-width: 1200px) {
  .typingAnimation {
    font-size: 24px;
    font-weight: bold;
    font-family: Arial, sans-serif;
    overflow: hidden;
    white-space: nowrap;
    letter-spacing: 0.15em;
    margin: 0 auto;

    color: white;
  }

  @keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }

  @keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: orange;
    }
  }

  .typing-animation {
    border-right: 0.15em solid orange;
    animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
  }
}
